html,
body {
  margin: 0px;
  padding: 0px;
  overflow-x: hidden;
  scroll-behavior: smooth;
}

body {
  width: 100%;
  /* font-family: "Sorts Mill Goudy", sans-serif; */
  font-family: "Lato", sans-serif;
  /* color: #2aabe4; */
  padding-bottom: 50px;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  justify-content: center;
  text-align: center;
  position: relative;
  color: #35477C;
}

.main-content{
  min-height: 90vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding-top: 8vh;
}

a {
  color: #35477C;
  text-decoration: none;
  padding: 5px 15px;
  transition: all 0.3s ease-in-out;
}


ul {
  list-style-type: none;
  margin: 0 20px;
  align-items: center;
  justify-content: center;
  text-decoration: none;
  list-style:none;
}


button {
  cursor: pointer;
  font-family: inherit;
  background: transparent;
  /* transform-origin: top left; */
}
button:active {
  transform: scale(0.98);
}

button:focus {
  outline: none;
}

.btn:hover {
  opacity: 0.9;
  transition-duration: 0s;
}

button:disabled {
  cursor: not-allowed;
}


.main-container{
  margin-top: 0;
  min-height: 100vh;
  width: 100%;
  /* background-image: url("./pages/pics/fatima_place.jpg"); */
}

.hero{
  /* margin-top: 10vh; */
  height: 90vh;
  width: 100%;
  /* background-image: url("./pages/pics/main.jpg"); */
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}

.hero-images{
  position: absolute;
display: flex;
height: 90vh;
width: 300vw;
transform: translateX(0);
/* background-color: #000; */
transition: transform 0.5s ease-in-out;
z-index: 5;
}
.hero-images .hero-image{
  height: 90vh;
  width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}
#hero1{
  background-image: url("./pages/pics/main.jpg");
}
#hero2{
  background-image: url("./pages/pics/people.jpg");
  /* width: 101vw; */
  /* transform: translateX(-1vw); */
}
#hero3{
  background-image: url("./pages/pics/place.jpg");
}

.hero-container-dim{
  position: absolute;
  width: 100%;
  height: 90vh;
  background-color: #000;
  opacity: 0.3;
  z-index: 2;
}

.hero-container{
  position: absolute;
  width: 100%;
  height: 90vh;
  color: #fff;
  opacity: 1;
  z-index: 3;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.hero-container > *{
  opacity: 1;
}

.hero-aboutus{
  /* margin-top: 10vh; */
  height: 90vh;
  width: 100%;
  background-image: url("./pages/pics/people.jpg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
}
/* header 1 */
#header1 {
  position: fixed;
  background-color: white;
  top: 0;
  left: 0;
  right: 0;
  transition: all 0.3s ease-in-out;
  padding: 0;
  margin: 0;
  z-index: 10;
  height: 10vh;
  box-shadow: 0px 0px 4px 0px #00000040;
  /* box-shadow: 0 2px 5px rgba(0, 0, 0, 1.3); */
}

#header1 #logo-fatima{
  display: flex;
  align-items: center;
}
#header1 .container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  /* padding: 10px 0; */
  transition: all 0.3s ease-in-out;
  height: 10vh;
}

#header1 h1{
  font-family: "Inter", sans-serif;
