  text-decoration: none;
  padding: 5px 15px;
  transition: all 0.3s ease-in-out;
  font-size: 20px;
}

#header1.active {
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 1.3);
