  padding-top: 56px;
  padding-bottom: 100px;
  margin-bottom: 10vh;
}

.donation-cycle-container{
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  max-width: 100%;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
}

.donation-steps{
  /* margin-top: 40px; */
    flex: 1 1 0%;
    display: flex;
    flex-direction: column;
    -webkit-box-align: center;
    align-items: center;
}

.step-content{
  margin-top: 44px;
    background-color: white;
    width: 220px;
    padding: 22px 32px 15px;
    border-radius: 16px;
    box-shadow: rgb(239 243 244) 0px 34px 64px;
    position: relative;
}

.step-content::before{
  content: "";
  position: absolute;
  width: 31px;
  height: 31px;
  background-color: white;
